import React from "react"
import { SnackbarProvider as NotistackProvider, SnackbarProviderProps } from "notistack"

type ProviderDefaults = Omit<SnackbarProviderProps, "children">

const SnackbarProvider: React.FC<SnackbarProviderProps> = ({ children, ...props }) => {
  const defaults: ProviderDefaults = {
    maxSnack: 3,
    dense: false,
    hideIconVariant: false,
    autoHideDuration: 2000,
    disableWindowBlurListener: true,
    anchorOrigin: {
      vertical: "top",
      horizontal: "right",
    },
  }

  return (
    <NotistackProvider {...defaults} {...props}>
      {children}
    </NotistackProvider>
  )
}

export { SnackbarProvider }
